/**
 * api로 받은 숫자의 데이터를 문자열로 바꿔준다.
 * @param {number} rank api로 받은 rank의 숫자
 * @returns {string} 숫자로 받은 tier를 문자열로 변환(0->아이언)
 */
export default function getTierNumberToString(
  tier: number,
  rank: number
): string {
  var _tier = "-";
  var _rank = "";

  switch (tier) {
    case 0:
      _tier = "아이언";
      break;
    case 1:
      _tier = "브론즈";
      break;
    case 2:
      _tier = "실버";
      break;
    case 3:
      _tier = "골드";
      break;
    case 4:
      _tier = "플래티넘";
      break;
    case 5:
      _tier = "다이아몬드";
      break;
    case 6:
      _tier = "마스터";
      break;
    case 7:
      _tier = "그랜드마스터";
      break;
    case 8:
      _tier = "챌린저";
      break;
  }

  switch (rank) {
    case 1:
      _rank = "1";
      switch (tier) {
        case 6:
          _rank = "";
          break;
        case 7:
          _rank = "";
          break;
        case 8:
          _rank = "";
          break;
      }
      break;
    case 2:
    case 3:
    case 4:
      _rank = rank.toString();
      break;
  }
  return `${_tier} ${_rank}`;
}
