import { navigate } from "@reach/router";
import React, { useState } from "react";
import getTierNumberToString from "../../../../../../../tools/generator/rank";

interface Props {
  icon: number;
  summonerName: string;
  rank: number;
  tier: number;
  leaguePoint: number;
  level: number;
}

function Summary(props: Props) {
  const [summary, setSummary] = useState(
    "flex w-[95%] h-[60px] cursor-pointer border-none bg-white p-[10px] overflow-hidden gap-[10px] items-center"
  );
  return (
    <button
      className={summary}
      onMouseEnter={() => {
        setSummary(
          "flex w-[95%] h-[60px] cursor-pointer border-none bg-[#eeeeee] p-[10px] overflow-hidden gap-[10px] rounded-lg items-center"
        );
      }}
      onMouseLeave={() => {
        setSummary(
          "flex w-[95%] h-[60px] cursor-pointer border-none bg-white p-[10px] overflow-hidden gap-[10px] items-center"
        );
      }}
      onMouseDown={() => {
        navigate("/POTG/" + props.summonerName).then(() => {
          window.location.reload();
        });
      }}
    >
      <img
        className="h-full rounded-lg m-0.5"
        src={`http://ddragon.leagueoflegends.com/cdn/12.12.1/img/profileicon/${props.icon}.png`}
        alt="Icon"
      />
      <div className="text-sm font-bold">{props.summonerName}</div>
      <div className="text-xs">
        {props.rank >= 0 ? (
          <>
            {getTierNumberToString(Number(props.tier), Number(props.rank))} -{" "}
            {props.leaguePoint}LP
          </>
        ) : (
          <>Level {props.level}</>
        )}
      </div>
    </button>
  );
}

export default Summary;
